.section {
  height: 690px;
  background: linear-gradient(#020c14, #003);
  clip-path: polygon(0% 0%, 50% 3%, 100% 0%, 100% 100%, 0 100%);
  transform: translate(0, -42%);
  -ms-transform: translate(0, 0);
  padding-top: 110px;
  position: relative;
  padding-top: 55px;
}

.wrapperParticles {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
}
.particles {
  height: 100%;
  width: 100%;
}
.footer {
  position: absolute;
  z-index: 999;
  bottom: 20px;
  left: 50%;
  transform: translate(-50%, 0);
  color: #fff;
  font-size: 12px;
  font-weight: 300;
  text-align: center;
}
@media(min-width: 1200px) {
  .section {
    height: 720px;
    clip-path: polygon(0% 0%, 50% 6%, 100% 0%, 100% 100%, 0 100%);
    transform: translate(0, -102%);
    -ms-transform: translate(0, 0);
    padding-top: 89px;
    position: relative;
  }
  .footer {
    font-size: 14px;
    bottom: 30px;
  }
  .preLoadBack {
    width: 0px;
    height: 0px;
    opacity: 0;
    background: url('../../img/background/actions.jpg'), url('../../img/background/portfoloo.jpg'), url('../../img/background/team.jpg'), url('../../img/background/90440.jpg'), url('../../img/background/services.jpg');
    background-image: -webkit-image-set(url('../../img/background/actions.webp') 1x, url('../../img/background/90440.webp') 1x, url('../../img/background/portfoloo.webp') 1x, url('../../img/background/team.webp') 1x, url('../../img/background/services.webp') 1x, url('../../img/background/actions.jpg') 1x, url('../../img/background/portfoloo.jpg') 1x, url('../../img/background/team.jpg') 1x, url('../../img/background/90440.jpg') 1x, url('../../img/background/services.jpg') 1x);
  }
}
