@media (min-width: 1200px) {
  .list {
    padding: 0;
    display: flex;
    list-style: none;
    justify-content: center;
    margin-bottom: 44px;
  }
  .listItem:not(:last-child) {
    margin-right: 6vw;
  }
  .link {
    cursor: pointer;
    color: #fff;
    font-size: 16px;
    font-weight: 300;
    text-decoration: none;
  }
  .link:hover, .link:focus, .link:active {
    color: #fff;
    text-decoration: none;
    outline: none;
  }
}
