.list {
  padding: 0;
  padding-top: 50px;
  list-style: none;
}
.contacts {
  font-size: 14px;
  font-weight: 300;
  text-align: center;
  color: #fff;
}
.contacts p {
  display: flex;
  align-items: center;
  justify-content: center;
}
.contacts p:not(:last-child) {
  margin-bottom: 15px;
}
.contacts p a {
  text-decoration: none;
  color: #fff;
  cursor: pointer;
}
.contacts p a:hover {
  text-decoration: none;
  color: #fff;
}
.contacts i {
  color: #69c9ca;
  margin-right: 10px;
}
@media(min-width: 1200px) {
  .list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 15px;
    padding: 53px 5vw 44px 5vw;

  }
  .header {
    position: relative;
    color: #fff;
    font-size: 24px;
    font-weight: 300;
    text-transform: uppercase;
    margin-bottom: 44px;
  }
  .logo {
    display: block;
    position: absolute;
    height: 10vh;
    width: auto;
    top: -106px;
  }
  .contacts p {
    justify-content: flex-start;
  }
  .contacts p:not(:last-child) {
    margin-bottom: 13px;
  }
  .contacts i {
    margin-right: 0.75vw;
  }
  .listItemServices {
    display: inline-flex;
    flex-direction: column;
    align-items: flex-start;
  }
  .listItemServicesHeader {
    font-size: 24px;
    font-weight: 300;
    color: #69c9ca;
    line-height: 1;
    margin-bottom: 27px;
    text-transform: uppercase;
  }
  .listItemServiceLink {
    font-size: 16px;
    font-weight: 300;
    line-height: 2;
    color: #fff;
    cursor: pointer;
    text-decoration: none;
  }
  .listItemServiceLink:hover, .listItemServiceLink:focus, .listItemServiceLink:active {
    color: #fff;
    text-decoration: none;
    outline: none;
  }
}
