.header {
  color: #fff;
  text-align: center;
  text-transform: uppercase;
  font-family: 'Poiret One';
  font-size: 30px;
  line-height: 30px;
}

@media(min-width: 1200px) {
  .header {
    height: 100px;
    line-height: 100px;
    background: url('../../../img/elements/buttonsGroup.png');
    background-image: -webkit-image-set(url('../../../img/elements/buttonsGroup.webp') 1x, url('../../../img/elements/buttonsGroup.png') 1x);
    background-repeat: no-repeat;
    background-position: 40% 100%;
    font-size: 60px;
  }
}
